body .App {
  background-color: #fff;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 18px; }

a {
  color: #00a2ff; }

h1 {
  border-bottom: 1px solid #d9d9d9; }

.container {
  width: 900px;
  margin: 0 auto; }

@media screen and (max-width: 968px) {
  .container {
    width: 100%;
    margin: 0 auto; } }

.carousel {
  position: relative;
  /* indicators to keep track of the carousel. We will use flexbox to center it! */
  /* next and previous buttons */ }

.carousel .item {
  position: relative;
  z-index: 0;
  /* if its active, display it (we won't use this in the react version!) */
  /* Fading animation */ }

.carousel .item img {
  width: 100%; }

.carousel .item .carousel-item {
  display: none; }

.carousel .item .active {
  display: block;
  background: #fff;
  pointer-events: none; }

.carousel .item .fade {
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s; }

@-webkit-keyframes fade {
  from {
    opacity: .6; }
  to {
    opacity: 1; } }

@keyframes fade {
  from {
    opacity: .6; }
  to {
    opacity: 1; } }

.carousel .indicators {
  list-style-type: none;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  z-index: 100;
  padding-left: 0;
  color: #fff; }

.carousel .indicators li {
  float: left; }

.carousel .indicators li button {
  color: #fff;
  border: 0;
  background: transparent;
  cursor: pointer;
  margin: 4px; }

.carousel .button-previous {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  border: none;
  padding: 0 20px;
  background: transparent;
  color: #fff;
  font-size: 80px;
  z-index: 100;
  cursor: pointer; }

.carousel .button-previous:hover {
  background: rgba(255, 255, 255, 0.5); }

.carousel .button-next {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  border: none;
  padding: 0 20px;
  background: transparent;
  color: #fff;
  font-size: 80px;
  z-index: 100;
  cursor: pointer; }

.carousel .button-next:hover {
  background: rgba(255, 255, 255, 0.5); }

